.radio-but__container {
  display: inline-flex; }

.radio-but__text {
  font-family: "Montserrat-Bold";
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #1F2041; }

.radio-but__input {
  display: none; }

.radio-but__label {
  display: inline-flex;
  align-items: center;
  font-family: "Montserrat-Regular";
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: rgba(31, 32, 65, 0.75);
  cursor: pointer;
  position: relative;
  margin-right: 20px; }
  .radio-but__label::before {
    content: '';
    color: #000;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
    text-align: center;
    text-indent: 0px;
    width: 20px;
    height: 20px;
    background: #FFF;
    vertical-align: middle;
    border: 1px solid rgba(31, 32, 65, 0.25);
    box-sizing: border-box;
    border-radius: 10px;
    transition: 0.3s; }

.radio-but__circle {
  left: 4px;
  z-index: 1;
  position: absolute;
  opacity: 0%;
  width: 12px;
  height: 12px;
  border-radius: 10px;
  background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
  border: 0px;
  box-shadow: none;
  transition: .3s; }

input:checked ~ .radio-but__label::before {
  border: 1px solid #BC9CFF; }
  input:checked ~ .radio-but__label::before .radio-but__circle {
    opacity: 100%; }

input:checked ~ .radio-but__label .radio-but__circle {
  opacity: 100%; }
