@charset "UTF-8";
.rating-star {
  display: inline-flex;
  font-family: Material Icons;
  font-size: 24px;
  line-height: 20px; }

.rating-star__item {
  background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
  /* Градиент для текста */
  -webkit-background-clip: text;
  /* Обрезаем фон по тексту */
  -webkit-text-fill-color: transparent;
  /* Прозрачный текст */ }
  .rating-star__item::after {
    content: 'star'; }
  .rating-star__item_empty {
    background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
    /* Градиент для текста */
    -webkit-background-clip: text;
    /* Обрезаем фон по тексту */
    -webkit-text-fill-color: transparent;
    /* Прозрачный текст */ }
    .rating-star__item_empty::after {
      content: 'star_border'; }
