  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  display: flex;
  justify-content: center; }
  .card-login__container {
    width: 320px; }
  .card-login__title {
    margin: 40px 0 20px 0;
