    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;
    color: #1F2041; }
  .card-login__email {
    margin-bottom: 10px; }
  .card-login__button {
    width: 320px;
    height: 44px;
    margin: 20px 0 30px 0; }
  .card-login__create {
    display: flex;
    justify-content: space-between; }
    .card-login__create p {
      font-family: "Montserrat-Regular";
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 18px; }
  .card-login__make-acc {
    width: 99px;
    height: 44px; }
