@charset "UTF-8";
.text-input {
  position: relative; }
  .text-input__text {
    font-family: "Montserrat-Bold";
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 15px;
    text-transform: uppercase;
    color: #1F2041; }
  .text-input__container {
    position: relative; }
  .text-input__box {
    font-family: "Montserrat-Regular";
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    color: rgba(31, 32, 65, 0.75);
    background: #FFFFFF;
    border: 1px solid rgba(31, 32, 65, 0.25);
    box-sizing: border-box;
    border-radius: 4px;
    padding-left: 15px;
    width: 260px;
    height: 44px;
    outline: none; }
    .text-input__box::placeholder {
      font-family: "Montserrat-Regular", sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      color: rgba(31, 32, 65, 0.25); }
    .text-input__box:hover, .text-input__box:focus {
      border: 1px solid rgba(31, 32, 65, 0.5); }
    .text-input__box:after {
      z-index: 1;
      font-family: 'Material Icons';
      font-size: 24px;
      content: 'arrow_forward';
      background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
      /* Градиент для текста */
      -webkit-background-clip: text;
      /* Обрезаем фон по тексту */
      -webkit-text-fill-color: transparent;
      /* Прозрачный текст */ }
  .text-input__w320 {
    width: 320px; }
  .text-input__icon {
    position: absolute;
    top: 10px;
    left: 225px; }
    .text-input__icon:after {
      font-family: 'Material Icons';
      font-size: 24px;
      background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
      /* Градиент для текста */
      -webkit-background-clip: text;
      /* Обрезаем фон по тексту */
      -webkit-text-fill-color: transparent;
      /* Прозрачный текст */
      content: 'arrow_forward'; }
