.room-num {
  display: flex;
  justify-content: space-between; }
  .room-num__num {
    font-family: "Montserrat-Bold";
    font-style: normal;
    font-weight: bold;
    font-size: 19px;
    line-height: 23px;
    color: #1F2041; }
  .room-num__num span {
    font-family: "Montserrat-Regular";
