.dropdown__box {
  font-size: 14px;
  line-height: 24px;
  color: rgba(31, 32, 65, 0.75);
  position: relative; }

.drop__title {
  font-family: "Montserrat-Bold";
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #1F2041;
  margin-bottom: 5px; }

.drop__text-box {
  display: flex;
  align-items: center;
  height: 44px;
  border: 1px solid rgba(31, 32, 65, 0.25);
  box-sizing: border-box;
  border-radius: 4px;
  background: #FFFFFF;
  color: rgba(31, 32, 65, 0.75);
  max-width: 266px;
  position: relative; }
  .drop__text-box::after {
    top: 10px;
    right: 10px;
    position: absolute;
    font-family: 'Material Icons';
    font-size: 24px;
    content: 'keyboard_arrow_down'; }

.drop__text-field {
  font-family: "Montserrat-Regular";
  color: rgba(31, 32, 65, 0.75);
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  box-sizing: border-box;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-left: 15px;
  width: 180px;
  outline: none;
  border: none; }

.dropdown_w320 {
  max-width: 320px; }

.drop__menu {
  width: 100%;
  display: none;
  position: absolute;
  top: 63px;
  background: #FFFFFF;
  border: 1px solid rgba(31, 32, 65, 0.5);
  border-radius: 0 0 4px 4px;
  box-sizing: border-box;
  z-index: 1; }

.visible {
  display: block; }

.dropdown__box_active {
  border: 1px solid rgba(31, 32, 65, 0.5);
  border-radius: 4px 4px 0 0; }

.item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 40px; }

.dropdown__clear {
  font-family: "Montserrat-Bold";
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #BC9CFF;
  margin: auto 13px;
  cursor: pointer; }

.dropdown__clear_unactive {
  visibility: hidden; }

.dropdown__submit {
  font-family: "Montserrat-Bold";
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #BC9CFF;
  margin: auto 7px;
  cursor: pointer;
  justify-self: end; }

.item__text {
  font-family: "Montserrat-Bold";
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #1F2041;
  padding-left: 15px;
  flex-grow: 1; }

.item__calc {
  padding-right: 7px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.item__value {
  font-family: "Montserrat-Bold";
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-align: center;
  color: #1F2041;
  width: 32px; }

.item__calc-dec,
.item__calc-inc {
  display: inline-block;
  height: 30px;
  width: 30px;
  border: 1px solid rgba(31, 32, 65, 0.25);
  box-sizing: border-box;
  border-radius: 22px;
  background-color: white;
  cursor: pointer;
  font-family: "Montserrat-Regular";
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  color: rgba(31, 32, 65, 0.5); }
  .item__calc-dec:focus,
  .item__calc-inc:focus {
    outline: none; }
  .item__calc-dec:active,
  .item__calc-inc:active {
    border: 1px solid rgba(31, 32, 65, 0.75); }
  .item__calc-dec:hover,
  .item__calc-inc:hover {
    border: 1px solid rgba(31, 32, 65, 0.5); }
  .item__calc-dec_unactive,
  .item__calc-inc_unactive {
    border: 1px solid rgba(31, 32, 65, 0.1); }
    .item__calc-dec_unactive:focus, .item__calc-dec_unactive:active, .item__calc-dec_unactive:hover,
    .item__calc-inc_unactive:focus,
    .item__calc-inc_unactive:active,
    .item__calc-inc_unactive:hover {
      border: 1px solid rgba(31, 32, 65, 0.1); }
