.card-reg {
  width: 380px;
  height: 646px;
  display: flex;
  justify-content: center;
  background: #FFFFFF;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px; }
  .card-reg .text-input__text {
    margin: 0;
    margin-bottom: 5px; }
  .card-reg .text-input {
    margin-bottom: 10px; }
  .card-reg__inp-mask {
    margin: 23px 0 20px 0; }
  .card-reg__container {
    width: 320px;
    margin-top: 40px; }
  .card-reg__title {
    font-family: "Montserrat-Bold";
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;
    color: #1F2041;
    margin-bottom: 20px; }
  .card-reg__toggle {
    margin-top: 10px; }
  .card-reg__button {
    width: 320px;
    height: 44px;
    margin: 20px 0 30px 0; }
  .card-reg__login {
    display: flex;
    justify-content: space-between; }
  .card-reg__login p {
    font-family: "Montserrat-Regular";
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 18px;
    text-align: center;
    color: #000000; }
  .card-reg__logbut {
    width: 99px;
    height: 44px; }
