.form-el {
  width: 1440px;
  margin: auto;
  background: white; }
  .form-el__container {
    margin: auto;
    display: grid;
    grid-template-columns: 320px 320px 320px;
    grid-template-rows: 200px;
    grid-auto-rows: minmax(100px, auto);
    width: 1104px;
    column-gap: 72px;
    margin-top: 65px; }
  .form-el__checkboxes {
    margin-bottom: 40px; }
  .form-el__button {
    height: 44px;
    width: 99px; }
  .form-el__lbut {
    height: 44px;
    width: 320px; }
  .form-el__buttons-container {
    display: grid;
    grid-template-columns: 99px 99px;
    column-gap: 20px;
    row-gap: 30px; }
  .form-el__text {
    font-family: "Montserrat-Bold";
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 15px;
    text-transform: uppercase;
    color: #1F2041;
    margin: 0;
    margin-bottom: 15px; }
  .form-el__text-field p {
    margin: 0;
    margin-bottom: 5px; }
  .form-el__pagination {
    margin-top: 40px; }
  .form-el__like-but {
    display: flex; }
  .form-el__like-but .like-butt {
    margin-right: 10px; }
  .form-el__bullet-text {
    font-family: "Montserrat-Bold";
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 15px;
    text-transform: uppercase;
    color: #1F2041; }

.form-el__toggle .toggle-but {
  margin-bottom: 10px; }
