.like-butt {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 20px;
  border-radius: 10px;
  background: rgba(31, 32, 65, 0.25); }
  .like-butt_active {
    background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%); }
  .like-butt__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 38px;
    height: 18px;
    background: #FFFFFF;
    box-sizing: border-box;
    border-radius: 10px;
    color: rgba(31, 32, 65, 0.25); }
  .like-butt__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 10px;
    line-height: 9px;
    height: 20px;
    width: 20px; }
    .like-butt__icon_active {
      color: #BC9CFF; }
  .like-butt__number {
    display: flex;
    align-items: center;
    height: 20px;
    width: 20px;
    font-family: "Montserrat-Regular";
    font-style: normal;
    font-weight: normal;
    font-size: 10px;
    line-height: 12px;
    text-align: center;
    padding-left: 2px; }
    .like-butt__number_active {
      color: #BC9CFF; }
