.card-tobook {
  background: #FFFFFF;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  width: 380px;
  height: 511px;
  display: flex;
  justify-content: center; }
  .card-tobook__container {
    margin-top: 30px;
    width: 320px; }
  .card-tobook__cal {
    margin-top: 20px; }
  .card-tobook__drop {
    margin-top: 20px;
    margin-bottom: 20px; }
  .card-tobook__prices {
    display: grid;
    grid-template-columns: 220px 20px minmax(55px, 80px);
    margin-top: 10px;
    font-family: "Montserrat-Regular";
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    color: rgba(31, 32, 65, 0.75); }
  .card-tobook__money, .card-tobook__money-sum {
    text-align: end; }
  .card-tobook__i {
    font-weight: bold;
    width: 20px;
    height: 20px;
    border: 1px solid rgba(31, 32, 65, 0.25);
    color: rgba(31, 32, 65, 0.25);
    box-sizing: border-box;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    vertical-align: baseline; }
  .card-tobook__result {
    display: flex;
    margin-top: 30px; }
  .card-tobook__restext, .card-tobook__sum {
    font-family: "Montserrat-Bold";
    font-style: normal;
    font-weight: bold;
    font-size: 19px;
    line-height: 23px;
    color: #1F2041; }
  .card-tobook__dash {
    flex-grow: 1;
    border-bottom: 1px dashed rgba(31, 32, 65, 0.25);
    box-sizing: border-box; }
  .card-tobook__button {
    margin-top: 20px;
    width: 320px;
    height: 44px; }

@media (max-width: 380px) {
  .card-tobook {
    width: 320px; }
  .card-tobook__container {
    width: 310px; }
  .card-tobook__button {
    width: 310px; } }
