.page-room-details {
  max-width: 1440px;
  margin: 0 auto;
  background: #E5E5E5; }
  .page-room-details__container {
    background: white;
    display: flex;
    justify-content: center;
    flex-direction: column; }
  .page-room-details__title {
    font-family: "Montserrat-Bold";
    font-style: normal;
    font-weight: bold;
    font-size: 19px;
    line-height: 23px;
    color: #1F2041; }
  .page-room-details__images {
    margin: 0 auto;
    max-width: 1440px;
    display: grid;
    grid-template-columns: minmax(300px, 981px) minmax(150px, 461px);
    grid-template-rows: 242px 242px; }
  .page-room-details__image1, .page-room-details__image2, .page-room-details__image3 {
    object-fit: cover; }
  .page-room-details__image1 {
    width: 100%;
    height: 100%;
    grid-row-start: 1;
    grid-row-end: 3; }
  .page-room-details__image2, .page-room-details__image3 {
    width: 100%;
    height: 100%; }
  .page-room-details__disc {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 70px;
    max-width: 1160px;
    margin: 0 auto;
    margin-top: 70px;
    padding-bottom: 60px; }
  .page-room-details__item3 {
    grid-row: 1 / span 2;
    grid-column: 3 / span 1; }
  .page-room-details__item4 {
    margin-top: 30px;
    grid-column: 1 / 3; }
  .page-room-details__item6 {
    max-width: 341px; }
  .page-room-details__item5 .page-room-details__title {
    margin-left: 20px; }
  .page-room-details__item5, .page-room-details__item6 {
    margin-top: 30px; }
  .page-room-details__comment {
    margin-top: 23px; }
  .page-room-details__comment-head {
    display: inline-flex;
    justify-content: space-between;
    max-width: 712px; }
  .page-room-details__text {
    font-family: "Montserrat-Regular";
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    color: rgba(31, 32, 65, 0.75);
    margin-top: 20px; }

.page-room-details__comment-head span {
  font-family: "Montserrat-Regular";
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 14px;
  text-align: right;
  color: rgba(31, 32, 65, 0.5);
  display: inline-flex;
  justify-content: space-between; }

@media (max-width: 1100px) {
  .page-room-details__disc {
    grid-template-columns: 1fr 1fr;
    padding: 0 2%;
    column-gap: 0px; }
  .page-room-details__item3 {
    grid-row: 1 / span 2;
    grid-column: 2 / span 1; }
  .page-room-details__item4 {
    margin-top: 30px;
    grid-column: 1 / span 2; } }

@media (max-width: 767px) {
  .page-room-details__disc {
    grid-template-columns: 1fr;
    padding: 0 1%; }
  .page-room-details__item3 {
    grid-row: auto;
    grid-column: auto; }
  .page-room-details__item4 {
    margin-top: 30px;
    grid-column: auto; }
  .page-room-details__comment-head {
    display: inline; } }

@media (max-width: 380px) {
  .page-room-details__item3 {
    display: flex;
    justify-content: center; } }

@media (max-width: 915px) {
  .page-room-details__images {
    grid-template-rows: 180px 180px; } }

@media (max-width: 490px) {
  .page-room-details__images {
    grid-template-columns: 1fr;
    grid-template-rows: 180px 180px 180px; }
  .page-room-details__image1 {
    grid-row-start: 1;
    grid-row-end: 1; } }
