.toggle-but__label {
  position: relative;
  display: inline-flex;
  align-items: center;
  cursor: pointer;
  font-family: "Montserrat-Regular";
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: rgba(31, 32, 65, 0.75);
  padding-left: 50px; }

.toggle-but__slider {
  height: 20px;
  width: 40px;
  position: absolute;
  cursor: pointer;
  background-color: white;
  top: 2px;
  left: 0;
  border: 1px solid rgba(31, 32, 65, 0.25);
  box-sizing: border-box;
  border-radius: 10px;
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  -webkit-transition: .4s;
  transition: .4s; }
  .toggle-but__slider::before {
    margin: 4px;
    content: "";
    height: 12px;
    width: 12px;
    background: rgba(31, 32, 65, 0.25);
    border-radius: 10px;
    -webkit-transition: .4s;
    transition: .4s; }

.toggle-but__input {
  display: none; }

input:checked + .toggle-but__slider {
  border: 1px solid #BC9CFF; }

input:checked + .toggle-but__slider::before {
  background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
  -webkit-transform: translateX(18px);
  -ms-transform: translateX(18px);
  transform: translateX(18px); }
