.rich-checkbox {
  width: 266px; }
  .rich-checkbox_bold {
    font-family: "Montserrat-Bold";
    color: rgba(31, 32, 65, 0.75); }
    .rich-checkbox_bold::before {
      margin-right: 10px; }
  .rich-checkbox__text {
    margin-left: 30px;
    font-family: "Montserrat-Regular";
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 14px;
    color: rgba(31, 32, 65, 0.5); }
  .rich-checkbox__name {
    font-family: "Montserrat-Bold";
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 15px;
    text-transform: uppercase;
    color: #1F2041;
    margin-bottom: 15px; }
