.footer {
  margin-top: 1px;
  margin-bottom: 40px; }
  .footer__bold-text {
    padding-top: 4px;
    padding-bottom: 21px; }
  .footer__colunm {
    display: flex;
    justify-content: center; }
    .footer__colunm:first-child {
      width: 160px; }
    .footer__colunm:last-child {
      width: 160px; }
  .footer__container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 372px;
    box-sizing: border-box;
    background-color: white;
    box-shadow: 0px 1px 0px rgba(31, 32, 65, 0.1);
    margin-bottom: 1px; }
  .footer__copy-container {
    width: 1160px;
    min-height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-between; }
  .footer__copy-text {
    padding: 10px 5px 10px 10px;
    display: block;
    font-family: "Montserrat-Regular", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: rgba(31, 32, 65, 0.75);
    text-align: center; }
  .footer__copyright {
    background-color: white;
    box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
    display: flex;
    justify-content: center;
    align-items: center; }
  .footer__disc {
    font-family: Montserrat-Regular, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 24px;
    color: rgba(20, 20, 26, 0.75);
    width: 260px;
    margin-top: -3px; }
  .footer__email {
    margin-top: 23px;
    width: 260px;
    height: 44px; }
  .footer__info {
    display: grid;
    grid-template-columns: minmax(270px, 300px) minmax(170px, 200px) minmax(130px, 200px) minmax(170px, 200px) 260px;
    max-width: 1160px;
    row-gap: 25px;
    margin-top: 5px;
    padding: 0 10px; }
  .footer__link-text {
    padding-bottom: 20px; }
  .footer__logo {
    padding-bottom: 19px; }
  .footer__socials {
    display: flex;
    justify-content: center; }
    .footer__socials:last-child {
      padding-right: 0px; }

@media (max-width: 1040px) {
  .footer__container {
    height: auto;
    padding: 40px 0; }
  .footer__info {
    grid-template-columns: minmax(270px, 300px) minmax(170px, 200px) minmax(130px, 200px);
    grid-template-rows: 170px 170px; }
  justify-content: center; }

@media (max-width: 610px) {
  .footer__copy-container {
    display: block; }
  .footer__info {
    grid-template-columns: minmax(270px, 300px) minmax(170px, 200px);
    grid-template-rows: 170px 170px 170px;
    row-gap: 10px; } }

@media (max-width: 490px) {
  .footer__info {
    grid-template-columns: minmax(150px, 270px);
    grid-template-rows: 135px 170px 170px 140px 160px; }
  .footer__container {
    padding: 10px 0 10px 0; } }
