.comment {
  font-family: "Montserrat-Regular";
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: rgba(31, 32, 65, 0.75);
  display: flex; }
  .comment__media {
    margin-right: 10px; }
  .comment__avatar {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 48px;
    height: 48px;
    border-radius: 32px;
    border: 2px solid #FFFFFF;
    box-sizing: border-box;
    filter: drop-shadow(0px 10px 20px rgba(31, 32, 65, 0.1));
    margin-bottom: 12px; }
  .comment__content {
    margin-right: 10px; }
  .comment__name {
    font-weight: 700; }
  .comment__date {
    margin-bottom: 10px; }
  .comment__text {
    max-width: 655px; }
