.header {
  position: relative;
  background-color: white;
  height: 70px; }
  .header__button-in {
    width: 87px;
    height: 34px;
    padding-right: 10px; }
  .header__button-reg {
    width: 196px;
    height: 34px; }
  .header__buttons {
    padding: 0 10px; }
    .header__buttons:last-child {
      padding-right: 0px; }
  .header__container {
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1160px;
    height: inherit; }
  .header__nav {
    display: flex;
    justify-content: flex-end; }
  .header__burger {
    position: relative;
    width: 40px;
    height: 35px;
    display: none;
    margin: 0 10px; }
    .header__burger::before, .header__burger::after, .header__burger span {
      height: 3px;
      width: 100%;
      position: absolute;
      background: #515758;
      margin: 0 auto; }
    .header__burger::before, .header__burger::after {
      content: ''; }
    .header__burger::before {
      top: 5px; }
    .header__burger span {
      top: 16px; }
    .header__burger::after {
      bottom: 5px; }
  .header__stick {
    width: 1px;
    height: 30px;
    background: rgba(31, 32, 65, 0.1);
    margin: 0 20px 0 10px; }
  .header__menu {
    display: flex;
    align-items: center; }
    .header__menu-item {
      display: flex;
      padding: 0 10px;
      align-items: center; }
      .header__menu-item:last-child {
        padding: 0; }
      .header__menu-item_current {
        color: rgba(31, 32, 65, 0.75);
        font-weight: bold; }
      .header__menu-item_arrow-down::after {
        font-family: 'Material Icons';
        font-size: 24px;
        content: 'keyboard_arrow_down'; }

@media (max-width: 915px) {
  .header__main-logo {
    margin-left: 30px; }
  .header__fixed-page {
    overflow: hidden; }
  .header {
    height: 60px; }
  .header__stick {
    display: none; }
  .header__menu-item {
    margin: 25px 0px 25px 33px;
    font-size: 14px; }
  .header__nav {
    display: block;
    position: absolute;
    top: 60px;
    left: 0;
    background: #FFFFFF;
    box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
    width: 100%;
    left: -125%;
    overflow: auto;
    transition: all 0.4s ease; }
  .header__nav.open-menu {
    left: 0;
    z-index: 20; }
  .header__burger {
    display: block;
    margin-right: 30px; }
  .header__burger.open-menu span {
    opacity: 0;
    transition: 0.5s; }
  .header__burger.open-menu::before {
    transform: rotate(38deg);
    top: 16px;
    transition: 0.4s; }
  .header__burger.open-menu::after {
    transform: rotate(-38deg);
    bottom: 16px;
    transition: 0.4s; } }
