@charset "UTF-8";
.benefits-desc__hr {
  width: 280px;
  height: 1px;
  background: rgba(31, 32, 65, 0.1);
  margin-top: 20px; }

.benefits-desc {
  margin-top: 20px;
  width: 280px; }

.benefits-desc__container {
  display: flex; }

.benefits-desc__icon {
  width: 48px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-style: normal;
  font-weight: normal;
  font-size: 48px;
  line-height: 41px;
  background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
  /* Градиент для текста */
  -webkit-background-clip: text;
  /* Обрезаем фон по тексту */
  -webkit-text-fill-color: transparent;
  /* Прозрачный текст */ }

.benefits-desc__block {
  padding-left: 10px; }

.benefits-desc__text,
.benefits-desc__title {
  display: inline-flex;
  font-family: "Montserrat-Bold";
  font-style: normal;
  font-size: 14px;
  line-height: 24px;
  color: rgba(31, 32, 65, 0.75); }

.benefits-desc__text {
  font-family: "Montserrat-Regular"; }
