.bullet-list {
  font-family: "Montserrat-Regular";
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: rgba(31, 32, 65, 0.75);
  list-style-type: none;
  width: 240px;
  position: relative;
  margin-bottom: 10px; }
  .bullet-list::before {
    top: 7px;
    left: -20px;
    position: absolute;
    content: ' ';
    background: rgba(31, 32, 65, 0.25);
    width: 10px;
    height: 10px;
    border-radius: 50%; }
  .bullet-list:last-child {
    margin: 0; }
  .bullet-list__ul {
    padding-left: 20px; }
