.card-select {
  background: #FFFFFF;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  width: 380px;
  height: 374px;
  display: flex;
  justify-content: center; }
  .card-select__container {
    max-width: 320px; }
  .card-select__title {
    margin-top: 40px;
    font-family: "Montserrat-Bold";
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;
    color: #1F2041; }
  .card-select__cal {
    margin-top: 21px; }
  .card-select__drop {
    margin-top: 21px; }
  .card-select__button {
    margin-top: 31px;
    width: 320px;
    height: 44px; }

@media (max-width: 380px) {
  .card-select__title {
    text-align: center; }
  .card-select__button {
    width: 300px; } }
