.range {
  width: 266px; }
  .range__text {
    font-family: "Montserrat-Bold";
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 15px;
    text-transform: uppercase;
    color: #1F2041;
    width: 140px; }
  .range__input {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px; }
  .range__input-box {
    font-family: "Montserrat-Regular";
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 15px;
    text-align: right;
    text-transform: uppercase;
    color: rgba(31, 32, 65, 0.5);
    outline: none;
    cursor: default;
    border: none;
    width: 120px; }
  .range__cont {
    z-index: 0;
    display: flex;
    align-items: center;
    width: 266px;
    height: 6px;
    background-color: #FFFFFF;
    position: relative;
    border-radius: 3px;
    box-sizing: border-box;
    border: 1px solid rgba(31, 32, 65, 0.25); }
  .range__bar {
    z-index: -1;
    position: absolute;
    height: 6px;
    background: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%);
    border-radius: 3px; }
  .range__max, .range__min {
    z-index: 3;
    cursor: default;
    user-select: none;
    position: absolute;
    left: 50px;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%);
    border: 2px solid #FFFFFF;
    border-radius: 10px;
    box-sizing: border-box; }
  .range__min {
    left: 5px; }
